    /**** #.overlay ****/
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    display: none;
    z-index: 10;
    justify-content: center;
    align-items: center;
  }
  
  /**** #.confirm-modal ****/
  .confirm-modal {
    max-width: 30rem;
    width: 100%;
    min-height: 25rem;
    padding-top: 2rem;
    background-color: #fff;
    -webkit-animation: fadeInDown 1s ease;
            animation: fadeInDown 1s ease;
    box-shadow: 0 0 7rem 0 #000;
  }
  
  /**** #.confirm-title ****/
  .confirm-title {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 1rem;
    margin-top: -10px;
  }
  
  /**** #.confirm-buttons ****/
  .confirm-buttons {
    display: flex;
    justify-content: center;
    max-width: 25rem;
    width: 100%;
    margin: auto;
  }
  
  /**** #.confirm-btn ****/
  .confirm-btn {
    display: inline-block;
    padding: 1rem;
    border: none;
    font-family: inherit;
    font-size: 1.5rem;
    font-weight: 400;
    transition: all 0.3s ease-in-out;
    color: #ffffff;
    cursor: pointer;
    width: 48%;
  }
  
  .confirm-btn:hover {
    box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.6);
  }
  
  /**** #.yes-btn ****/
  .yes-btn {
    background-color: #e74c3c;
  }

  img{
      width: 50%;
      height: 50%;
      margin-bottom: 1rem;
  }
/* http://meyerweb.com/eric/tools/css/reset/
   v3.0 | 20180413
   License: none (public domain)
*/

html,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */

*[hidden] {
  display: none;
}



ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*#my reset
=====================-*/

*,
*:before,
*:after {
  /* Нужно для мобильного WebKit */
  /* Нужно для Firefox */
  box-sizing: border-box;
}

/*#link
=====================-*/

a {
  text-decoration: none;
  color: inherit;
}

/*#image
=====================-*/

img {
  max-width: 100%;
  height: auto;
}

/*#input
=====================-*/

input,
textarea,
button,
.button,
.btn {
  font-family: inherit;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"],
.button,
.btn {
  cursor: pointer;
}

input:focus,
textarea:focus,
button:focus,
.button:focus,
.btn:focus {
  outline: none;
}
/*#table
=====================-*/
table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

